/*hm开头的表示仅为 Hello MUI示例定义*/
.hm-description {
	margin: 0.5em 0;
}

.hm-description > li {
	font-size: 14px;
	color: #8f8f94;
}

#outterFrame {
	position: fixed;
	top: 0;
	bottom: 47px;
	left: 0;
	right: 0;
	overflow: auto;
}
footer.footer-bar {
	display: table;
	width: 100%;
	position: fixed;
	left: 0;
	bottom: 0;
	height: p2m(100);
	background: #fff;
	z-index: 100;
	//border-top: 1px solid #bcbcbc;

	a {
		display: table-cell;
		vertical-align: middle;
		width: 20%;
		//border-right: 1px solid #bcbcbc;
		color: #626262;
		text-align: center;
		font-size: 12px;
		line-height: 1.1em;

		&.foot-R {
			color: #d11711;
		}

		img {
			width: 26px;
			height: 26px;
			display: inline-block;
		}

		&:nth-of-type(3) {
			position: absolute;

			img {
				z-index: 101;
				margin-top: -7px;
				width: 1rem;
				height: 1rem;
				background-color: #fff;
				border-radius: 100px;
				box-shadow: 0px -2px 6px rgba(0, 0, 0, 0.3);
			}

			div {
				z-index: 102;
				text-align: center;
			}
		}
		&.foot-bar-a.curr {
			color: #348c96;
		}
	}
}
